<template>
    <Header></Header>
    <div class="container min-height padding-top-default">
        <div class="dashboard-main z-index">
            <LeftBar :status="status"></LeftBar>
            <div class="right-bar">
                <router-view></router-view>
            </div>
            <span class="account-menu-overflow" v-on:click="closeMenu" :class="status && 'active'"></span>
            <span class="account-menu" v-on:click="showMenu" :class="status && 'active'"></span>
        </div>
    </div>
    <Footer></Footer>
</template>

<script>
import Header from '@/components/Header.vue'
import LeftBar from '@/components/Account/LeftBar.vue'
import Footer from '@/components/Footer.vue'

export default {
    data() {
        return {
            status: false
        }
    },
    components: {
        Header,
        LeftBar,
        Footer
    },
    watch: {
        $route() {
            this.closeMenu()
        }
    },
    methods: {
        showMenu() {
            this.status = true
        },
        closeMenu() {
            this.status = false
        }
    }
}
</script>