<template>
    <div class="row m-order" v-if="profile">
        <template v-if="profile.verification.value == 1 || profile.verification.value == 4">
            <div class="col-12 col-md-12 col-lg-12 col-xl-6">
                <span class="icon-text times" v-if="profile.verification.value == 4">{{ profile.verification.name }}</span>
                <div class="dashboard-personal-wrap">
                    <form v-on:submit.prevent="onsubmit" class="form" autocomplete="off">
                        <label class="form-label">Имя</label>
                        <div class="form-group mb-4">
                            <input type="text" name="fisrtname" class="form-input" v-model="firstname" placeholder="Введите Имя" required :disabled="loading">
                        </div>
                        <label class="form-label">Фамилия</label>
                        <div class="form-group mb-4">
                            <input type="text" name="lastname" class="form-input" v-model="lastname" placeholder="Введите Фамилию" required :disabled="loading">
                        </div>
                        <label class="form-label">Дата рождения</label>
                        <div class="form-group mb-4">
                            <input type="date" name="date-birth" class="form-input" v-model="dateBirth" required :disabled="loading">
                        </div>
                        <label class="form-label">Номер паспорта</label>
                        <div class="form-group mb-4">
                            <input type="text" name="passport-number" class="form-input" v-model="passportNumber" placeholder="Введите номер паспорта" required :disabled="loading">
                        </div>
                        <label class="form-label">ИНН</label>
                        <div class="form-group mb-4">
                            <input type="number" name="inn" class="form-input" v-model="inn" placeholder="Введите ИНН" required :disabled="loading">
                        </div>
                        <label class="form-label">Страна</label>
                        <div class="form-group mb-4">
                            <input type="text" class="form-input" placeholder="Выберите страну" readonly :value="selectName" v-on:click="showSelectModal">
                        </div>
                        <label class="form-label">Город</label>
                        <div class="form-group mb-4">
                            <input type="text" name="city" class="form-input" v-model="city" placeholder="Введите город" required :disabled="loading">
                        </div>
                        <label class="form-label">Адрес</label>
                        <div class="form-group mb-4">
                            <input type="text" name="address" class="form-input" v-model="address" placeholder="Введите адрес" required :disabled="loading">
                        </div>
                        <div class="form-group mb-4">
                            <label class="file-label">
                                <input type="file" class="d-none" @change="passportPreview">
                                <span v-if="!passportPhotoPreview">Документ, удостоверяющий личность</span>
                                <img v-if="passportPhotoPreview" :src="passportPhotoPreview">
                            </label>
                        </div>
                        <div class="form-group mb-4">
                            <label class="file-label">
                                <input type="file" class="d-none" @change="addressPreview">
                                <span v-if="!addressPhotoPreview">Документ, подтверждающий адрес проживания</span>
                                <img v-if="addressPhotoPreview" :src="addressPhotoPreview">
                            </label>
                        </div>
                        <button class="button button-success block lg mt-4" type="submit" :disabled="loading">Отправить</button>
                    </form>
                </div>
            </div>
            <div class="col-12 col-md-12 col-lg-12 col-xl-6">
                <p class="text success mt-4">Идентификация счета необходима для предотвращения случаев мошенничества, противодействия отмыванию средств и финансирования терроризма.</p>
                <p class="text success">- В поле «документ, удостоверяющий личность» загрузите сканированную копию или фото внутреннего или загран паспорта, водительское удостоверение, военный билет.</p>
                <p class="text success">- В поле «документ, подтверждающий адрес проживания» загрузите сканированную копию или фото страницы прописки в паспорте (только если в качестве документа подтверждающего личность вы загрузили внутренний паспорт), либо любой официальный документ с печатью и адресом проживания на котором указаны ваши данные.</p>
            </div>
        </template>
    </div>
    <template v-if="profile && profile.verification.value == 2">
        <div class="dashboard-personal-wrap">
            <span class="icon-text my-4 check">{{ profile.verification.name }}</span>
        </div>
    </template>
    <template v-if="profile && profile.verification.value == 3">
        <div class="dashboard-personal-wrap">
            <span class="icon-text my-4 clock">{{ profile.verification.name }}</span>
        </div>
    </template>
    <Select :data="SelectData"></Select>
</template>

<script>
import Select from '@/components/Account/Select'

export default {
    data() {
        return {
            firstname: '',
            lastname: '',
            dateBirth: '',
            passportNumber: '',
            inn: '',
            city: '',
            email: '',
            phone: '',
            SelectData: {
                title: 'Выберите страну',
                list: '',
                ListName: 'name',
                ListValue: 'code'
            },
            passportPhoto: null,
            passportPhotoPreview: '',
            addressPhoto: null,
            addressPhotoPreview: ''
        }
    },
    components: {
        Select
    },
    created() {
        if(this.profile) {
            this.firstname = this.profile.firstname
            this.lastname = this.profile.lastname
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        selectName() {
            return this.$store.getters.selectName
        },
        profile() {
            return this.$store.getters.getProfile
        },
        country() {
            return (this.$store.getters.country)
            ? this.$store.getters.country
            : this.$store.dispatch('getCountry')
        }
    },
    watch: {
        profile() {
            if(this.profile) {
                this.firstname = this.profile.firstname
                this.lastname = this.profile.lastname
            }
        },
        country() {
            this.SelectData.list = this.country
        }
    },
    methods: {
        showSelectModal() {
            this.$store.dispatch('setModalStatus', true)
        },
        passportPreview(e) {
            this.passportPhoto = e.target.files[0]
            this.passportPhotoPreview = URL.createObjectURL(this.passportPhoto)
        },
        addressPreview(e) {
            this.addressPhoto = e.target.files[0]
            this.addressPhotoPreview = URL.createObjectURL(this.addressPhoto)
        },
        onsubmit() {
            const formData = new FormData()
            formData.append('firstname', this.firstname)
            formData.append('lastname', this.lastname)
            formData.append('date_birth', this.dateBirth)
            formData.append('passport_id', this.passportNumber)
            formData.append('inn', this.inn)
            formData.append('country', this.$store.getters.selectValue)
            formData.append('city', this.city)
            formData.append('address', this.address)
            formData.append('photo_passport', this.passportPhoto)
            formData.append('photo_address', this.addressPhoto)
            this.$store.dispatch('verification', formData)
        }
    }
}
</script>