<template>
    <div>
        <h1 class="dashboard-title">Мои кошельки</h1>
        <Splide :options="options" v-if="balances && !loading">
            <SplideSlide v-for="balance in balances" :key="balance">
                <div class="balance-wrap">
                    <div class="balance-body">
                        <img :src="balance.logo" :alt="balance.name">
                        <div class="balance-text">
                            <h3>
                                <b>{{ balance.currency }}</b>
                                <span>{{ balance.balance }}</span>
                            </h3>
                            <h2>
                                <b>{{ balance.name }}</b>
                                <span v-if="balance.rate" :class="String(balance.difference).search('-') !== -1 ? 'text-danger' : 'text-success'">${{ balance.rate }}</span>
                            </h2>
                        </div>
                    </div>
                    <div class="balance-footer">
                        <router-link :to="'/dashboard/refill/cryptocurrency?currency=' + balance.currency" class="button md block" v-if="balance.can_deposit === true">Ввод</router-link>
                        <a class="button md block disabled" v-else>Ввод</a>
                        <router-link :to="'/dashboard/withdraw/' + balance.currency" class="button md block" v-if="balance.can_withdraw === true">Вывод</router-link>
                        <a class="button md block disabled" v-else>Вывод</a>
                    </div>
                </div>
            </SplideSlide>
        </Splide>
        <div class="row m-order">
            <div class="col-12 col-md-12 col-lg-12 col-xl-8">
                <div class="dashboard-personal-wrap">
                    <h1 class="title">История аккаунта</h1>
                    <div class="dashboard-table">
                        <div class="row dashboard-table-row table-header h">
                            <div class="col-md-4 col-xl-4 dashboard-table-column">Дата</div>
                            <div class="col-md-4 col-xl-5 dashboard-table-column">Действие</div>
                            <div class="col-md-4 col-xl-3 dashboard-table-column">IP адрес</div>
                        </div>
                        <div class="row dashboard-table-row nth"></div>
                        <template v-if="actions">
                            <div class="row dashboard-table-row nth m" v-for="item in actions" :key="item">
                                <div class="col-12 col-md-4 col-xl-4 dashboard-table-column"><span class="md-block">Дата:</span>{{ item.date }}</div>
                                <div class="col-12 col-md-4 col-xl-5 dashboard-table-column"><span class="md-block">Действие:</span>{{ item.action }}</div>
                                <div class="col-12 col-md-4 col-xl-3 dashboard-table-column"><span class="md-block">IP адрес:</span>{{ item.ip }}</div>
                            </div>
                        </template>
                    </div>
                </div>
            </div>
            <div class="col-12 col-md-12 col-lg-12 col-xl-4">
                <div class="row">
                    <div class="col-lg-12 col-xl-12">
                        <div class="dashboard-personal-wrap" v-if="profile">
                            <h1 class="title">Верификация аккаунта</h1>
                            <div class="profile-info">
                                <div class="photo-wrap">
                                    <img :src="profile.avatar + '?v=1.0.0'" :alt="profile.email">
                                </div>
                                <div>
                                    <b>{{ profile.firstname +' '+ profile.lastname }}</b>
                                    <span v-if="profile.verification.value == 1" class="text-danger">{{ profile.verification.name }}</span>
                                    <span v-if="profile.verification.value == 2" class="text-success">{{ profile.verification.name }}</span>
                                    <span v-if="profile.verification.value == 3" class="text-warning">{{ profile.verification.name }}</span>
                                    <span v-if="profile.verification.value == 4" class="text-danger">{{ profile.verification.name }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-6 col-xl-12">
                        <div class="dashboard-personal-wrap" v-if="profile">
                            <h1 class="title">Лимиты за текущий день</h1>
                            <div class="profile-info-column refill">
                                <div>
                                    <span>Пополнение</span>
                                    <b>{{ profile.limits.refill }} USDT</b>
                                </div>
                            </div>
                            <div class="profile-info-column buy">
                                <div>
                                    <span>Покупка</span>
                                    <b>{{ profile.limits.buy }} USDT</b>
                                </div>
                            </div>
                            <div class="profile-info-column sell">
                                <div>
                                    <span>Продажа</span>
                                    <b>{{ profile.limits.sell }} USDT</b>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-6 col-xl-12">
                        <div class="dashboard-personal-wrap" v-if="profile">
                            <h1 class="title">Персональные данные</h1>
                            <div class="profile-info-column calendar">
                                <div>
                                    <span>Дата регистрации</span>
                                    <b>{{ profile.date_register }}</b>
                                </div>
                            </div>
                            <div class="profile-info-column exit">
                                <div>
                                    <span>Последний вход</span>
                                    <b>{{ profile.last_access }}</b>
                                </div>
                            </div>
                            <div class="profile-info-column email">
                                <div>
                                    <span>Электронная почта</span>
                                    <b>{{ profile.email }}</b>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Splide, SplideSlide } from '@splidejs/vue-splide'
import '@splidejs/vue-splide/css'

export default {
    data() {
        return {
            options: {
                type: 'loop',
                perPage: 3,
                gap: '20px',
                arrows: false,
                pagination: false,
                autoplay: true,
                interval: 2500,
                pauseOnHover: true,
                keyboard: 'global',
                breakpoints: {
                    1366: {
                        perPage: 3,
                    },
                    1200: {
                        perPage: 2,
                    },
                    992: {
                        perPage: 2,
                    },
                    768: {
                        perPage: 1,
                    },
                },
            }
        }
    },
    components: {
        Splide,
        SplideSlide,
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        balances() {
            return this.$store.getters.getCurrencies
        },
        profile() {
            return this.$store.getters.getProfile
        },
        actions() {
            return (this.$store.getters.getActionList)
            ? this.$store.getters.getActionList
            : this.$store.dispatch('getActionList')
        }
    },
}
</script>